// const colorWhite = 0;
const colorBlack = 1;

const debugLog = false;

function log(...args) {
  if (debugLog) {
    console.log(...args);
  }
}

export class Edge {
  constructor(x, maxY, minY, k_reciprocal) {
    this.x = x;
    this.oriX = x;
    this.maxY = maxY;
    this.minY = minY;
    this.k_reciprocal = k_reciprocal;
    // this.next = null;
  }
}

/**
 * 
 */
export class EdgeTable {

  constructor(points, fillInConstrutor = true) {
    points = points.filter(item => item.color === colorBlack);
    let [minY, maxY] = [Math.min(...points.map(xy => xy.y)), Math.max(...points.map(xy => xy.y))]
    this.nodes = [];
    for (let i = minY; i <= maxY; i++) {
      this.nodes[i] = [];
    }
    if (fillInConstrutor) {
      this.fillEdgeTable(points);
    }
  }

  fillEdgeTable(points) {
    log(' ', points);

    let edges = [] //

    for (let i = 0; i < points.length; i++) {
      //
      let p1 = points[i];
      let p2 = points[(i + 1) % points.length];
      log('p1 : ', p1, "p2:", p2);


      //
      // let startX = Math.min(p1.x, p2.x);
      let maxY = Math.max(p1.y, p2.y);
      let k = (p2.y - p1.y) / (p2.x - p1.x);
      let k_reciprocal = 1 / k;


      //y,y
      let minY = Math.min(p1.y, p2.y);
      let XofMinY = p1.y < p2.y ? p1.x : p2.x;

      let curEdge = new Edge(XofMinY, maxY, minY, k_reciprocal);
      this.nodes[minY].push(curEdge);

      edges.push(curEdge);
      log('ET', JSON.parse(JSON.stringify(this.nodes)));
    }

    //
    for (let i = 0; i < edges.length; i++) {
      let curEdge = edges[i];
      let nextEdge = edges[(i + 1) % edges.length];

      if (curEdge.maxY === nextEdge.minY) {
        curEdge.maxY -= 1;
      }
      else if (curEdge.minY === nextEdge.maxY) {
        nextEdge.maxY -= 1;
      }
    }



    //
    for (let i = 0; i < this.nodes.length; i++) {
      if (!this.nodes[i]) {
        continue;
      }
      this.nodes[i].sort((a, b) => a.x - b.x);
    }

  }

}

function getMinY(points) {

  return Math.min(...points.map(xy => xy.y));

}

function getMaxY(points) {

  return Math.max(...points.map(xy => xy.y));

}


/**
 * 
 * @param {Point[]} points 
 * @param {Function} fillFunc - y,x1,x2,fill
 */
export function scanFill(points, fillFunc) {

  //* 1.ETAET
  let ET = new EdgeTable(points, true);
  // let AET = new EdgeTable(figure, false);
  let AET = [];

  //
  let startY = getMinY(points);
  let endY = getMaxY(points);

  //* 2.ETAET
  for (let curY = startY; curY <= endY; curY++) {
    if (ET.nodes[curY].length > 0) {
      log('ET.nodes: ', ET.nodes);
      AET = AET.concat(ET.nodes[curY]);
      break;
    }
  }
  log("AET:", AET);


  //* 3.AET

  let curY = startY;

  while (AET.length > 0) {
    log('Y: ', curY);
    log("AET:", AET);

    let onFill = false; //
    let lastX = 0;      //x
    //AET
    for (let node of AET) {
      onFill = !onFill;

      if (onFill === false) {
        //onFillfalse,
        //
        //x
        //ceil
        let leftX = lastX;
        leftX = Math.ceil(leftX);
        //-1floor
        let rightX = node.x;
        rightX = rightX % 1 === 0
          ? rightX - 1
          : Math.floor(rightX);

        //
        log(': ', leftX, rightX);
        fillFunc(curY, leftX, rightX);  //* 

      }
      //

      lastX = node.x;

    }

    //* 4.YY
    //y>=maxY
    let _curY = curY;
    AET = AET.filter(node => node.maxY > _curY);

    //y=y+1
    curY += 1;
    for (let node of AET) {
      node.x += node.k_reciprocal;
    }
    //YAET
    if (ET.nodes[curY]) {
      AET = AET.concat(ET.nodes[curY]);
    }
    //x
    AET.sort((a, b) => {
      return (a.x - b.x) === 0 ? (a.k_reciprocal - b.k_reciprocal) : (a.x - b.x);
    })

    //


  }
}


export let testRun = () => {
  let figure = {
    points: [
      { x: 0, y: 0 },
      { x: 100, y: 0 },
      { x: 100, y: 100 },
      { x: 0, y: 100 },
    ]
  }
  scanFill(figure);
}

